"use client"

import { useState } from "react"
import AdminLayout from "@/components/AdminLayout"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Table, TableHeader, TableBody, TableRow, TableHead, TableCell } from "@/components/ui/table"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts"

interface FinancialRecord {
  id: string
  cabang: string
  tanggal: string
  jenis: "Pemasukan" | "Pengeluaran"
  deskripsi: string
  jumlah: number
}

const BRANCHES = [
  "Cabang Batu Akik",
  "Cabang Christoper Mihing",
  "Cabang Anang Santawi",
  "Cabang Tidar",
]

export default function FinancePage() {
  const [records, setRecords] = useState<FinancialRecord[]>([])
  const [error, setError] = useState("")
  const [selectedBranch, setSelectedBranch] = useState<string>("all")
  const [selectedPeriod, setSelectedPeriod] = useState<string>("daily")
  const [formData, setFormData] = useState({
    cabang: "",
    tanggal: new Date().toISOString().split("T")[0],
    jenis: "Pemasukan" as "Pemasukan" | "Pengeluaran",
    deskripsi: "",
    jumlah: "",
  })

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("id-ID", {
      style: "currency",
      currency: "IDR",
      minimumFractionDigits: 0,
    }).format(amount)
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  const handleSelectChange = (name: string, value: string) => {
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    setError("")

    // Validation
    if (!formData.cabang || !formData.deskripsi || !formData.jumlah) {
      setError("Mohon lengkapi semua field yang wajib diisi")
      return
    }

    // Add new record
    const newRecord: FinancialRecord = {
      id: Date.now().toString(),
      ...formData,
      jumlah: parseFloat(formData.jumlah),
    }

    setRecords((prev) => [...prev, newRecord])

    // Reset form except cabang
    setFormData((prev) => ({
      ...prev,
      deskripsi: "",
      jumlah: "",
    }))
  }

  const getFilteredRecords = () => {
    let filtered = [...records]
    
    if (selectedBranch !== "all") {
      filtered = filtered.filter((record) => record.cabang === selectedBranch)
    }

    // Sort by date
    filtered.sort((a, b) => new Date(a.tanggal).getTime() - new Date(b.tanggal).getTime())

    return filtered
  }

  const prepareChartData = () => {
    const filtered = getFilteredRecords()
    const data: any[] = []

    filtered.forEach((record) => {
      const existingData = data.find((d) => d.tanggal === record.tanggal)
      
      if (existingData) {
        if (record.jenis === "Pemasukan") {
          existingData.pemasukan += record.jumlah
        } else {
          existingData.pengeluaran += record.jumlah
        }
      } else {
        data.push({
          tanggal: record.tanggal,
          pemasukan: record.jenis === "Pemasukan" ? record.jumlah : 0,
          pengeluaran: record.jenis === "Pengeluaran" ? record.jumlah : 0,
        })
      }
    })

    return data
  }

  return (
    <AdminLayout>
      <div className="space-y-8">
        <div>
          <h1 className="text-3xl font-bold">Keuangan</h1>
          <p className="text-gray-500 mt-2">Kelola dan pantau keuangan semua cabang</p>
        </div>

        {/* Input Form */}
        <Card className="p-6">
          <h2 className="text-xl font-semibold mb-4">Input Transaksi Keuangan</h2>
          
          {error && (
            <Alert variant="destructive" className="mb-4">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <label className="text-sm font-medium">Cabang</label>
                <Select
                  value={formData.cabang}
                  onValueChange={(value) => handleSelectChange("cabang", value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Pilih Cabang" />
                  </SelectTrigger>
                  <SelectContent>
                    {BRANCHES.map((branch) => (
                      <SelectItem key={branch} value={branch}>
                        {branch}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <label className="text-sm font-medium">Tanggal</label>
                <Input
                  type="date"
                  name="tanggal"
                  value={formData.tanggal}
                  onChange={handleInputChange}
                  required
                />
              </div>

              <div className="space-y-2">
                <label className="text-sm font-medium">Jenis Transaksi</label>
                <Select
                  value={formData.jenis}
                  onValueChange={(value: "Pemasukan" | "Pengeluaran") => handleSelectChange("jenis", value)}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Pilih Jenis" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Pemasukan">Pemasukan</SelectItem>
                    <SelectItem value="Pengeluaran">Pengeluaran</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <label className="text-sm font-medium">Jumlah</label>
                <Input
                  type="number"
                  name="jumlah"
                  value={formData.jumlah}
                  onChange={handleInputChange}
                  placeholder="Masukkan jumlah"
                  required
                />
              </div>

              <div className="space-y-2 md:col-span-2">
                <label className="text-sm font-medium">Deskripsi</label>
                <Input
                  name="deskripsi"
                  value={formData.deskripsi}
                  onChange={handleInputChange}
                  placeholder="Masukkan deskripsi transaksi"
                  required
                />
              </div>
            </div>

            <Button type="submit" className="w-full md:w-auto">
              Simpan Transaksi
            </Button>
          </form>
        </Card>

        {/* Financial Report */}
        <Card className="p-6">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6">
            <h2 className="text-xl font-semibold">Laporan Keuangan</h2>
            <div className="flex flex-col md:flex-row gap-4 mt-4 md:mt-0">
              <Select
                value={selectedBranch}
                onValueChange={setSelectedBranch}
              >
                <SelectTrigger className="w-[200px]">
                  <SelectValue placeholder="Pilih Cabang" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Semua Cabang</SelectItem>
                  {BRANCHES.map((branch) => (
                    <SelectItem key={branch} value={branch}>
                      {branch}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>

              <Select
                value={selectedPeriod}
                onValueChange={setSelectedPeriod}
              >
                <SelectTrigger className="w-[200px]">
                  <SelectValue placeholder="Pilih Periode" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="daily">Harian</SelectItem>
                  <SelectItem value="weekly">Mingguan</SelectItem>
                  <SelectItem value="monthly">Bulanan</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Chart */}
          <div className="h-[400px] mb-8">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={prepareChartData()}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="tanggal" />
                <YAxis />
                <Tooltip formatter={(value) => formatCurrency(Number(value))} />
                <Legend />
                <Line
                  type="monotone"
                  dataKey="pemasukan"
                  stroke="#10b981"
                  name="Pemasukan"
                />
                <Line
                  type="monotone"
                  dataKey="pengeluaran"
                  stroke="#ef4444"
                  name="Pengeluaran"
                />
              </LineChart>
            </ResponsiveContainer>
          </div>

          {/* Transactions Table */}
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Tanggal</TableHead>
                  <TableHead>Cabang</TableHead>
                  <TableHead>Jenis</TableHead>
                  <TableHead>Deskripsi</TableHead>
                  <TableHead>Jumlah</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {getFilteredRecords().map((record) => (
                  <TableRow key={record.id}>
                    <TableCell>{record.tanggal}</TableCell>
                    <TableCell>{record.cabang}</TableCell>
                    <TableCell>
                      <span className={`px-2 py-1 rounded-full text-xs ${
                        record.jenis === "Pemasukan"
                          ? "bg-green-100 text-green-800"
                          : "bg-red-100 text-red-800"
                      }`}>
                        {record.jenis}
                      </span>
                    </TableCell>
                    <TableCell>{record.deskripsi}</TableCell>
                    <TableCell>{formatCurrency(record.jumlah)}</TableCell>
                  </TableRow>
                ))}
                {records.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={5} className="text-center text-gray-500">
                      Belum ada data keuangan
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </div>
        </Card>
      </div>
    </AdminLayout>
  )
}
