"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"

interface AdminLayoutProps {
  children: React.ReactNode
}

export default function AdminLayout({ children }: AdminLayoutProps) {
  const router = useRouter()
  const [activePath, setActivePath] = useState("")

  useEffect(() => {
    const isAuthenticated = localStorage.getItem("isAuthenticated")
    if (!isAuthenticated) {
      router.push("/login")
    }
    setActivePath(window.location.pathname)
  }, [router])

  const handleLogout = () => {
    localStorage.removeItem("isAuthenticated")
    router.push("/login")
  }

  const menuItems = [
    { path: "/dashboard", label: "Dashboard" },
    { path: "/members-transactions", label: "Data Member & Transaksi" },
    { path: "/finance", label: "Operasional Gym" },
    { path: "/financial-reports", label: "Laporan Keuangan" },
  ]

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Sidebar */}
      <div className="fixed left-0 top-0 w-64 h-full bg-white shadow-lg">
        <div className="p-4">
          <div className="flex items-center justify-center mb-8">
            <img 
              src="/bsc-gym-logo.png.png" 
              alt="BSC GYM Logo" 
              className="h-16 w-auto"
            />
          </div>
          <nav className="space-y-2">
            {menuItems.map((item) => (
              <Link 
                key={item.path} 
                href={item.path}
                className={`block p-3 rounded-lg transition-colors ${
                  activePath === item.path
                    ? "bg-black text-white"
                    : "hover:bg-gray-100"
                }`}
              >
                {item.label}
              </Link>
            ))}
          </nav>
        </div>
      </div>

      {/* Main Content */}
      <div className="ml-64 min-h-screen">
        {/* Header */}
        <header className="bg-white shadow-sm">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 h-16 flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <img 
                src="/bsc-gym-logo.png.png" 
                alt="BSC GYM Logo" 
                className="h-10 w-auto"
              />
              <h1 className="text-xl font-bold">BSC GYM</h1>
            </div>
            <Button 
              variant="outline"
              onClick={handleLogout}
            >
              Keluar
            </Button>
          </div>
        </header>

        {/* Page Content */}
        <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          {children}
        </main>
      </div>
    </div>
  )
}
